a<template>
  <div class="section">
        <div class="content">
            <h2>but wait, there's more!</h2>
            <h3>GSAP3 released in November 2019</h3>
            <ul class="animate-in">
                <li>~Major Improvements to file size: half the size of GSAP2</li>
                <li>~Rebuilt as modern ES modules</li>
                <li>~GSAP3 supports back to IE9</li>
                <li>~Combined Lite/Max into one GSAP object and one timeline object with simplified parameters</li>
                <li>~New and Expanded Features:
                    <ul class="sub-ul">
                        <li>~Advanced Staggering Capabilities</li> 
                        <li>~Relative timeline postioning</li>
                        <li>~Refresh values on looping tween</li>
                        <li>~Access to global timeline</li>
                        <li>~New Utility Methods</li>
                    </ul>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { gsap } from "gsap";
import { SplitText } from "gsap/SplitText";
export default {
  name: 'Slide5',
  data() {
      return{
          triggered: false
      }
  },
  mounted: function() {
    let animatedElements = this.$el.querySelector(".animate-in");
    let split = new SplitText(animatedElements, {type: 'lines'});
    gsap.set(split.lines, {opacity: 0, x: -20, immediateRender: true})
    this.animateIn = split;
  },
  methods: {
      onEnter: function() {
          if (this.triggered === false) {
            gsap.to(this.animateIn.lines, {duration: 1, opacity: 1, x: 0, stagger: 1, immediateRender: true})
            this.triggered = true;
          }
      }
  }
}
</script>

