import Vue from 'vue'
//import 'fullpage.js/vendors/scrolloverflow' // Optional. When using scrollOverflow:true
//import './fullpage.scrollHorizontally.min' // Optional. When using fullpage extensions
import VueFullPage from 'vue-fullpage.js'
import { gsap } from "gsap";
import { TextPlugin } from "gsap/TextPlugin";
import { SplitText } from "gsap/SplitText";
import { DrawSVGPlugin } from "gsap/DrawSVGPlugin.js";
import { MotionPathPlugin } from "gsap/MotionPathPlugin.js";
import { GSDevTools } from "gsap/GSDevTools.js";
gsap.registerPlugin(TextPlugin, SplitText, MotionPathPlugin, DrawSVGPlugin, GSDevTools);

Vue.use(VueFullPage, gsap, TextPlugin, SplitText, MotionPathPlugin, DrawSVGPlugin, GSDevTools);

import "prismjs";
import "prismjs/themes/prism-tomorrow.css";
import "prismjs/components/prism-scss.min";
import "prismjs/plugins/autolinker/prism-autolinker.min";
import "prismjs/plugins/autolinker/prism-autolinker.css";
import 'prismjs/plugins/line-numbers/prism-line-numbers';
import Prism from "vue-prism-component";
Vue.component("prism", Prism);


import App from './App.vue'

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')

  