<script>

import { gsap } from "gsap";
import { TextPlugin } from "gsap/TextPlugin";
import { SplitText } from "gsap/SplitText";
import { DrawSVGPlugin } from "gsap/DrawSVGPlugin.js";
import { MotionPathPlugin } from "gsap/MotionPathPlugin.js";
import { GSDevTools } from "gsap/GSDevTools.js";
gsap.registerPlugin(TextPlugin, SplitText, MotionPathPlugin, DrawSVGPlugin, GSDevTools);

export default {
  name: "logo",
  props: [],
  data() {
    return {
      tl: null
    }
  },
  mounted() {
    
    gsap.registerEffect({
      name: "drawLine",
      effect: (targets, config) => {
        let tl = gsap.timeline({});

        tl.to(config.dot, {duration: .25, opacity: 1, ease: "none"});
        
        targets.forEach(function(path, i){
        let $duration = config.duration == "ratio" ? path.getTotalLength()/250 : config.duration;
        let dotWidth = path.getAttribute("data-dot-width");
        let label = "path" + i + "-=.12";

        tl.from(path, {duration: $duration, drawSVG: 0, ease:"sine.inOut"}, label);

        if (dotWidth !== null) {
          tl.to(config.dot, {duration: .1, attr: {r: dotWidth}, ease: "sine.inOut"}, label);
        }

        tl.to(config.dot, {
          motionPath: {
            path: path,
            align: path,
            offsetX: config.offsetX,
            offsetY: config.offsetY,
            immediateRender: true
          },
          duration: $duration,
          ease: "sine.inOut",
          stagger: -.04
        },label);

        if (i == (targets.length-1)){
          tl.to(config.dot, {duration: .5, scale: 0, transformOrigin: "center center", ease: "back.in(2.5)"}, ">-.35");
        }
        });

        return tl
      },
      extendTimeline: true
    });

    let masterTl = gsap.timeline({ paused: true})

    let tl1 = gsap.timeline({delay: .5});
    tl1.drawLine(".logo-container .long-path .path", {dot: ".logo-container .long-path .target", duration: 6.5, offsetX: .8, offsetY: 26.5}, "start")
    tl1.drawLine(".logo-container .n .path", {dot: ".logo-container .n .target", duration: 1, offsetX: .9, offsetY: 25.5}, ">-.75")
    tl1.drawLine(".logo-container .a1 .path", {dot: ".logo-container .a1 .target", duration: .5, offsetX: 1, offsetY: 26}, "start+=1.5")
    tl1.from(".logo-container .a1-arch", {duration: .8, drawSVG: 0, ease: "sine.inOut", stagger: .1}, "<+.5");
    tl1.drawLine(".logo-container .e .path", {dot: ".logo-container .e .target", duration: 1.5, offsetX: -.7, offsetY: 26}, "start+=2.5")
    tl1.drawLine(".logo-container .a2 .path", {dot: ".logo-container .a2 .target", duration: .5, offsetX: 1, offsetY: 26}, "start+=3.25")
    tl1.from(".logo-container .a2-arch", {duration: .8, drawSVG: 0, ease: "sine.inOut", stagger: .1}, "<+.5");
    tl1.from(".logo-container .a-mask", {duration: .05, opacity: 0}, ">");


    let tl2 = gsap.timeline({delay: 2.75});
    tl2.from(".logo-container .logo .path-1", {duration: 2, drawSVG: 0, ease: "sine.inOut"},"start");
    tl2.from(".logo-container .logo .path-2", {duration: 2, drawSVG: 0, ease: "sine.inOut"}, "<.15");
    tl2.from(".logo-container .logo .path-3", {duration: 2, drawSVG: 0, ease: "sine.inOut"},"start");
    tl2.from(".logo-container .logo .path-4", {duration: 2, drawSVG: 0, ease: "sine.inOut"}, "<.15");
    tl2.from(".logo-container .logo .path-5", {duration: 2.25, drawSVG: 0, ease: "sine.inOut"},"start");
    tl2.from(".logo-container .logo-mask", {duration: .05, opacity: 0}, "start+=2");

    let tl3 = gsap.timeline({delay: 3.25});
    tl3.drawLine(".logo-container .jump-path1 .path", {dot: ".logo-container .jp1-target", duration: "ratio", offsetX: .45, offsetY: 26}, "start")
    tl3.drawLine(".logo-container .t .path", {dot: ".logo-container .t .target", duration: .5, offsetX: .5, offsetY: 26.25}, "start+=2")
    tl3.from(".logo-container .i2-dot", {opacity: 1}, "start+=2.90");
    tl3.from(".logo-container .i2-dot", {duration: .75, opacity: 0, y: 70, scale: .8, transformOrigin: "center center", ease: "back.out"}, "start+=2.90");
    tl3.from(".logo-container .i2-path", {duration: 1, drawSVG: 0, ease: "sine.inOut", stagger: .1}, "start+=3.15");

    let tl4 = gsap.timeline({delay: 4.25});
    tl4.addLabel("start", 0);
    tl4.drawLine(".logo-container .jump-path2 .path", {dot: ".logo-container .jp2-target", duration: "ratio", offsetX: .45, offsetY: 26}, "start");
    tl4.from(".logo-container .g-arch", {duration: .8, drawSVG: 0, ease: "sine.inOut", stagger: .1}, ">-1");
    tl4.from(".logo-container .i1-dot", {opacity: 1}, "start+=.75");
    tl4.from(".logo-container .i1-dot", {duration: .75, opacity: 0, y: 70, scale: .8, transformOrigin: "center center", ease: "back.out"}, "start+=.75");
    tl4.from(".logo-container .i1-path", {duration: 1, drawSVG: 0, ease: "sine.inOut", stagger: .1}, "start+=1");

    masterTl.add(tl1, "start")
            .add(tl2, "start")
            .add(tl3, "start")
            .add(tl4, "start")

    this.tl = masterTl;

    GSDevTools.create({animation: masterTl, container: ".logo-container", css: {top: "auto", position: "relative", y: "100%", marginBottom: "100px"}});
  }, 
  methods: {
    playTimeline: function() {
      this.tl.play();
    }
  }
}
</script>

