<template>
  <div class="section">
        <div class="content">
            <div class="slide">
                <h2>The Timeline Object</h2>
                <p>It's where the magic happens</p>
            </div>
            <div class="slide">
                <ul>
                    <li>~Allows you to both chain tweens and place them relative to eachother in the timeline context</li>
                    <li>~You canbe really specific with timing</li>
                    <li>~Child tweens can inherit parent properties as fallbacks</li>
                    <li>~Play controls easy accessibly through methods</li>
                    <li>~Timelines can be nested into a master timeline</li>
                </ul>
                <img src="@/assets/masterTimeline.png" alt="combined timelines"/>
            </div>
            <div class="slide">
                <img class="timeline-img" src="@/assets/timeline.png" alt="GSAP Timeline" />
                <animated-photo ref="animatedPhoto"></animated-photo>
            </div>
        </div>
    </div>
</template>

<script>
import AnimatedPhoto from '../../components/AnimatedPhoto.vue';

export default {
  name: 'Slide7', 
  components: {
    AnimatedPhoto
  }, 
  data() {
      return {
        triggered: false,
      }
  },
  methods: {
      onEnter: function() {
          if (this.triggered === false) {
            this.$refs.animatedPhoto.playTimeline();
            this.triggered = true;
          }
      }
  }
}
</script>

