<template>
  <div v-on:afterLoad="onEnter" class="section">
        <div class="content">
            <ul class="animate-in">
                <li>Caitlin Gleason</li>
                <li>Front End Engineer at Datto Inc.</li>
                <li>Likes: Hobbits, Manatees, SVGs, Artsy Web Things<sup>TM</sup></li>
            </ul>
            <animated-photo ref="animatedPhoto"></animated-photo>
        </div>
    </div>
</template>

<script>

import AnimatedPhoto from '../../components/AnimatedPhoto.vue';
import { gsap } from "gsap";
import { SplitText } from "gsap/SplitText";

export default {
  name: 'Slide2', 
  components: {
    AnimatedPhoto
  }, 
  data() {
      return {
        animateIn: [],
        triggered: false,
      }
  },
  mounted: function() {
    let animatedElements = this.$el.querySelector(".animate-in");
    let split = new SplitText(animatedElements, {type: 'lines'});
    gsap.set(split.lines, {opacity: 0, x: -20, immediateRender: true})
    this.animateIn = split;
  },
  methods: {
      onEnter: function() {
          if (this.triggered === false) {
            gsap.to(this.animateIn.lines, {duration: 1, opacity: 1, x: 0, stagger: 1, immediateRender: true})
            this.$refs.animatedPhoto.playTimeline();
            this.triggered = true;
          }
      }
  }
}
</script>

